body{
    padding: 0px;
    margin: 0px;
}
h1 {
    color : #06283D;
    font-size: 28px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}
/* Card {
    transform: scale(1.5);
    box-shadow: grey 5px;
} */

/* /////////////////////////////////// Navbar */
.nav-dropdown-scroll {
    max-height: 500px;
    overflow-y: scroll;
  }
#navContainer {
    margin: 0rem 0.5rem;
}
.nav-link {
    color: white;
    margin: 0rem;
    margin-left: 10px;
    text-decoration: none;

}

#_navbar {
    background-color: #06283D;
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}
.feedback:hover {
    color: rgb(207, 207, 207);
}
.aboutUs {
    float: right;
    padding-left: 50px;
}
.aboutUs:hover {
    color: rgb(207, 207, 207);
}
.aboutus {
    margin-top: 100px;
}
/* //////////////////////////////////// FOOTER */

#footer_div {
    background-color: #06283D;
    width: 100%;
    height: 2.5rem;
    margin-top: 50px;
    position: relative;
    bottom: 0;
}
#footer_para {
    text-align: center;
    font-size: 1rem;
    color: white;
}
#div_para {
    padding-top: 10px;
}

/* ///////////////////////////////////// Card */

.card_scale:hover {
    transform: scale(1.1);
}
.card-carousal {
    margin : 20px;
    margin-top: -10px;
}

/* //////////////////////////////////// sidebar */

#sidebar {
    background-color: #06283D;
    margin-top: 55px;
    padding-top: 30px;
    padding-bottom: 20px;
    width: 20%;
}

ul {
    font-size: 16px;
    list-style-type: none;
}

ul li {
    margin-bottom: 5px;
}

ul li a:hover{
    color: lightgreen;
}

ul li a{
    text-decoration: none;
    color: white;
}

/* //////////////////////////////////// tutorials */

#tutorials-body {
    padding-top: 100px;
    text-align: justify;
}

#left {
    float: left;
}
#right {
    float: right;
}

/* //////////////////////////////////////// code keeper */

.code-clipboard {
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    padding: 10px;
}

#clipboard-button {
    padding: 10px 15px;
    /* background-color: #007bff; */
    float: right;
    display: grid;
    color: #007bff;
    border: none;
    cursor: pointer;
}

[id^="code-snippet"] {
    text-align: left;
}
/* //////////////////////////////////// Feedback */

#feedback-form {
    padding: 80px;
}
#feedback-vector {
    margin: auto;
}

/* ////////////////////////////////////// Card Carousal */
.skillsContainer {
    margin-top : 100px;
}
.row1 {
    margin : 90px 0px 30px 30px;
}
.row2 {
    margin : 100px 0px -60px 30px;
}

/* /////////////////////////////////////////// search bar */
.search-input {
    position: relative; /* Make the parent container relative */
}

.search-input input {
    padding-right: 40px; /* Make room for the icon */
    border-radius: 20px;
  }
  
  .search-input .icon {
    position: absolute; /* Position the icon absolutely */
    top: 8px;
    bottom: 8px;
    right: 10px;
  }

  /* ////////////////////////////////////// Sidebar */
  /* ////////////////////////////////////// DropDown */
button#nextDis[disabled] {
    cursor : not-allowed;
}